.quote-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(145deg, #3a3a3a, #222);
    color: #e6e6e6;
}

.quote-box {
    padding: 20px;
    border: 2px solid #fff;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
    text-align: center;
    max-width: 600px;
    width: 90%;
}

.quote-text {
    font-size: 1.8rem;
    font-weight: 500;
    margin-bottom: 15px;
}

.quote-author {
    font-size: 1.2rem;
    color: #ccc;
    margin-top: 5px;
}

.new-quote-btn {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #444;
    border: none;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease;
}

.new-quote-btn:hover {
    background-color: #666;
    transform: scale(1.05);
}

@media (max-width: 768px) {
    .quote-text {
        font-size: 1.4rem;
    }

    .new-quote-btn {
        font-size: 0.9rem;
    }
}
